.button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  margin: 10px;
  padding: 16px 24px;
  transition: background-color .3s ease-in-out;
  display: inline-block;
}

.button:hover, .button:hover {
  background-color: #3e8e41;
}

.button_start:disabled {
  opacity: .5;
}

.container {
  justify-content: center;
  display: flex;
}

